<template>
    <v-row style="width: 100%;height: 100%">
        <v-card style="width: 25%">
            <GraphList v-bind:graphs="graphs"/>
        </v-card>
        <v-card style="width: 75%;">
            <GraphShowcase v-bind:graphs="graphs"/>
        </v-card>
    </v-row>
</template>

<style scoped>

</style>

<script>
import GraphList from '../components/GraphsList'
import GraphShowcase from '../components/GraphShowcase'

export default {
    components: {
        GraphList,
        GraphShowcase
    },
    data: () => ({
        graphs: [
            {name: 'Grafo di Petersen', subtitle: 'il grafo di Petersen è un grafo non orientato con 10 nodi e 15 archi', description: 'Il grafo di Petersen è fortemente regolare. È anche simmetrico, che significa che è transitivo sugli archi e transitivo sui nodi. Più specificamente è 3-transitivo sugli archi. Il grafo di Petersen non ha alcun ciclo hamiltoniano. È il più piccolo grafo cubico privo di ponti senza cicli hamiltoniani. È ipohamiltoniano, che significa che sebbene non abbia cicli hamiltoniani, cancellare un qualsiasi vertice lo rende hamiltoniano, e in questo ambito è il più piccolo grafo ipohamiltoniano. Il grafo di Petersen ha numero cromatico 3, che significa che i suoi nodi possono essere colorati con tre colori (ma non con due) in modo tale che nessuno spigolo colleghi vertici dello stesso colore.', url: 'https://it.wikipedia.org/wiki/Grafo_di_Petersen', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/91/Petersen1_tiny.svg/220px-Petersen1_tiny.svg.png'},
            {name: 'Grafo di Clebsch', subtitle: 'il grafo di Clebsch è un grafo 5-regolare con 16 nodi e 40 archi.', description: 'Il grafo di Clebsch a è hamiltoniano, connesso e non euleriano. Il grafo di Clebsch può essere colorato con quattro colori. Contiene come sottografo indotto il grafo di Grötzsch , il più piccolo grafo quadricromico privo di triangoli , e ogni sottografo indotto da quattro cromatismi del grafo di Clebsch è un supergrafo del grafo di Grötzsch.', url: 'https://it.qaz.wiki/wiki/Clebsch_graph', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/f/ff/Clebsch_graph.svg/1200px-Clebsch_graph.svg.png'},
            {name: 'Grafo di Dyck', subtitle: 'il grafo di Dyck è un 3-grafo regolare con 32 vertici e 48 spigoli.', description: 'Il grafo di Dyck è hamiltoniano con 120 cicli Hamiltoniani distinti. È anche 3-regolare e bipartito ', url: 'https://it.qaz.wiki/wiki/Dyck_graph', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/e/e8/Dyck_graph.svg/450px-Dyck_graph.svg.png'},
            {name: 'Grafo di Herschel', subtitle: 'il grafo di Herschel è un grafo bipartito non orientato con 11 vertici e 18 archi.', description: 'Il grafo di Herschel è planare, quindi segue dal teorema di Steinitz che è un grafo poliedrico : esiste un poliedro convesso (un enneaedro) avente il grafo di Herschel come scheletro. Questo poliedro ha nove quadrilateri per le facce. Essendo un grafo bipartito che ha un numero dispari di vertici, il grafo di Herschel non contiene un ciclo hamiltoniano. Tutti i vertici del grafo di Herschel tranne tre hanno il grado tre.', url: 'https://it.qaz.wiki/wiki/Herschel_graph', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/a/a9/Herschel_graph_no_col.svg/1024px-Herschel_graph_no_col.svg.png'},
            {name: 'Grafo Young Fibonacci', subtitle: 'il grafo di Young-Fibonacci, prende il nome di Alfred Giovane e Leonardo Fibonacci, è una struttura che coinvolge sequenze dei numeri 1 e 2.', description: 'Il grafo di Young-Fibonacci è un grafo infinito, con un vertice per ogni stringa delle cifre “1” e “2” (compresa la stringa vuota).', url: 'https://it.qaz.wiki/wiki/Young%E2%80%93Fibonacci_lattice', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/b/bd/Young-Fibonacci.svg/1024px-Young-Fibonacci.svg.png'},
            {name: 'Grafo di Grötzsch', subtitle: 'il grafo di Grötzsch è un grafo senza triangoli con 11 vertici, 20 archi.', description: 'Il grafo di Grötzsch è un membro di una sequenza infinita di grafi privi di triangoli. L\'esistenza del grafo di Grötzsch dimostra che l\'assunzione di planarità è necessaria nel teorema di Grötzsch ( Grötzsch 1959 ) secondo cui ogni grafo planare senza triangoli è tricolore.', url: 'https://it.qaz.wiki/wiki/Gr%C3%B6tzsch_graph', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/4/4b/Gr%C3%B6tzsch_graph.svg/1069px-Gr%C3%B6tzsch_graph.svg.png'},
            {name: 'Grafo di Wagner', subtitle: 'il grafo di Wagner è un grafo 3-regolare con 8 nodi e 12 archi.', description: 'Il grafico Wagner è senza triangoli. Possiede circuiti hamiltoniani ma non euleriani e non è bipartito.', url: 'https://it.qaz.wiki/wiki/Wagner_graph', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/c/c9/Wagner_graph_ham.svg/1024px-Wagner_graph_ham.svg.png'},
            {name: 'Grafi completi', subtitle: 'un grafo completo è un grafo semplice nel quale ogni vertice è collegato direttamente a tutti i vertici rimanenti.', description: 'I grafi completi con n vertici sono tutti isomorfi. In questi grafi vi sono n(n-1)/2 archi. I grafi completi sono i grafi massimamente connessi, in quanto l\'unico taglio di vertici che li sconnette è l\'insieme di tutti i suoi vertici.', url: 'https://it.wikipedia.org/wiki/Grafo_completo#:~:text=regolare%20di%20grado-,.,di%20tutti%20i%20suoi%20vertici.', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/7/73/Complete_graph_K8.svg/1024px-Complete_graph_K8.svg.png'},
            {name: 'Grafo cubico', subtitle: 'un grafo cubico è un grafo in cui tutti i vertici hanno grado tre. In altre parole un grafo cubico è un grafo 3-regolare. I grafi cubici sono chiamati anche grafi trivalenti.', description: 'Se un grafo cubico è scelto uniformemente in modo aleatorio fra tutti i grafi cubici con n vertici, allora è molto probabile che sia hamiltoniano: la proporzione dei grafi cubici con n vertici che sono hamiltoniani tende al limite a uno quando n va a infinito. L\'ampiezza del cammino di qualsiasi grafo cubico con n vertici è al massimo di n/6. Consegue dal lemma delle strette di mano, dimostrato da Eulero nel 1736 come parte del primo studio sulla teoria dei grafi, che ogni grafo cubico ha un numero pari di vertici.', url: 'https://it.wikipedia.org/wiki/Grafo_cubico', src: 'https://upload.wikimedia.org/wikipedia/commons/thumb/e/e4/3-cube_column_graph.svg/1024px-3-cube_column_graph.svg.png'},
            //{name: '', subtitle: '', description: '', url: '', src: ''}
        ]
    })
}
</script>
